export function clearRedirectData(redirectWindowData) {
  clearInterval(redirectWindowData.timer);
}

export function setRedirectWindowData(redirectWindowData, redirectWindow, sendCancelEvent, paymentMethodType) {
  const timer = setInterval(function () {

    if (redirectWindowData.window.closed) {
      clearInterval(timer);
      sendCancelEvent(paymentMethodType);
    }

  }, 1000);

  redirectWindowData.timer = timer;
  redirectWindowData.window = redirectWindow;
}
